$(function() {
    $('.m-btn')
        .on('mouseenter', function(e) {
            var parentOffset = $(this).offset(),
                relX = e.pageX - parentOffset.left,
                relY = e.pageY - parentOffset.top;
            $(this).find('._animate-span').css({top:relY, left:relX})
        })
        .on('mouseout', function(e) {
            var parentOffset = $(this).offset(),
                relX = e.pageX - parentOffset.left,
                relY = e.pageY - parentOffset.top;
            $(this).find('._animate-span').css({top:relY, left:relX})
        });
});

butter.init({cancelOnTouch: true});

gsapAnimation()


function gsapAnimation() {

    let header = document.querySelector(".header")
    let footer = document.querySelector(".footer")
    let indexPage = document.querySelector(".index-page")
    let worksPage = document.querySelector(".works-page")
    let workItemPage = document.querySelector(".work-item-page")
    let contactsPage = document.querySelector(".contacts-page")
    let servicePage = document.querySelector(".service-page")
    let aboutPage = document.querySelector(".about-page")
    if(header){
        headerAnim()
    }
    if(footer){
        footerAnim()
    }
    if(indexPage){
        indexAnim()
    }
    if(worksPage){
        worksPageAnim()
    }
    if(workItemPage){
        workItemPageAnim()
    }
    if(contactsPage){
        contactsPageAnim()
    }
    if(servicePage){
        servicePageAnim()
    }
    if(aboutPage){
        aboutPageAnim()
    }
    function headerAnim() {
        const tlHeader = gsap.timeline({})
        tlHeader.from(header, {
            y: -100,
            ease: "power4.out",
            duration: .3,
        })
    }
    function footerAnim() {
        const tlFooter = gsap.timeline({})
        tlFooter.from(footer, {
            y: 60,
            ease: "power4.out",
            duration: .3,
            scrollTrigger: {
                trigger: footer,
                start: 'top bottom',
                toggleActions: 'play none none reverse'
            }
        })
    }



    function indexAnim() {
        let title = document.querySelector(".main .h1")
        let rows = title.querySelectorAll(".heading-line__cover")
        const tlMain = gsap.timeline({})

        tlMain.from(".main__desc", {
            delay: .2,
            duration: .5,
            opacity: 0,
        }).to(rows, {
            duration: 1.5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        },'<').from(".main .text-bg", {
            duration: .75,
            opacity: 0,
            yPercent: 100,
        },'<').from(".main .portfolio-items", {
            duration: .35,
            opacity: 0,
            y: 100,
        },'<');

        portfolioCardsAnim()

        let serviceTitle = document.querySelector(".services .h2")
        let serviceTitleRows = serviceTitle.querySelectorAll(".heading-line__cover")

        const tlServices = gsap.timeline({
            scrollTrigger: {
                trigger: ".services",
                start: 'top bottom',
                toggleActions: 'play none none reverse'
            }
        })

        tlServices.from(".services__desc", {
            duration: 1,
            opacity: 0,
        }).to(serviceTitleRows, {
            duration: .5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        },"<").from(".services .text-bg", {
            duration: .5,
            opacity: 0,
            yPercent: 100,
        },'-=.5')


        const tlFeedback = gsap.timeline({
            scrollTrigger: {
                trigger: ".feedback",
                start: 'top bottom',
                toggleActions: 'play none none reverse'
            }
        })

        let feedbackLinks = document.querySelectorAll(".feedback .feedback__link")

        tlFeedback.from(".feedback__title", {
            duration: .5,
            opacity: 0,
        }).from(".feedback__desc", {
            duration: .3,
            opacity: 0,
        }).from(feedbackLinks, {
            duration: .3,
            yPercent: 100,
            opacity: 0,
            ease: "power4.out",
            stagger: 0.2,
        })

    }

    function worksPageAnim() {
        let title = document.querySelector(".works .h2")
        let rows = title.querySelectorAll(".heading-line__cover")
        const tlWorks = gsap.timeline({})

        tlWorks.to(rows, {
            delay: .2,
            duration: 1.5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        }).from(".works .text-bg", {
            duration: .75,
            opacity: 0,
            yPercent: 100,
        },'<').from(".works .portfolio-items", {
            duration: .75,
            opacity: 0,
            y: 100,
        },'-=.5');

        portfolioCardsAnim()
    }

    function workItemPageAnim() {
        let title = document.querySelector(".works .h2")
        let rows = title.querySelectorAll(".heading-line__cover")
        const tlWorks = gsap.timeline({})

        tlWorks.to(rows, {
            delay: .2,
            duration: 1.5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        }).from(".work-info__desc", {
            duration: .5,
            opacity: 0,
        },'-=.8').from(".work-info__site", {
            duration: .5,
            opacity: 0,
        },'<').from(".works .work-detail", {
            duration: .5,
            opacity: 0,
            y: 100,
        },'<');

        let workImages = Array.from(document.querySelectorAll('.work-detail__img'));
        workImages.forEach(card => {
            gsap.from(card, {
                y: 50,
                duration: .7,
                opacity: 0,
                scrollTrigger: {
                    trigger: card,
                    // start: 'top bottom',
                    toggleActions: 'play none none reverse'
                }
            })
        })
    }

    function contactsPageAnim() {
        let title = document.querySelector(".contacts .h2")
        let rows = title.querySelectorAll(".heading-line__cover")
        const tlWorks = gsap.timeline({})

        tlWorks.to(rows, {
            delay: .2,
            duration: 1.5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        }).from(".contacts .text-bg", {
            duration: 1,
            opacity: 0,
            yPercent: 100,
        },'<').from(".contacts__bottom", {
            duration: 1,
            opacity: 0,
            y: 100,
        },'<');
    }

    function servicePageAnim() {
        let title = document.querySelector(".service-item .h2")
        let rows = title.querySelectorAll(".heading-line__cover")
        const tlService = gsap.timeline({})

        tlService.to(rows, {
            delay: .2,
            duration: 1.5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        }).from(".service-item .text-bg", {
            duration: 1,
            opacity: 0,
            yPercent: 100,
        },'<').from(".service-item__desc", {
            duration: 1,
            opacity: 0,
            y: 100,
        },'<').from(".service-item__list", {
            duration: 1,
            opacity: 0,
            y: 100,
        },'<');

        let serviceItems = Array.from(document.querySelectorAll('.service-group'));
        serviceItems.forEach(card => {
            gsap.from(card, {
                y: 50,
                duration: .7,
                opacity: 0,
                scrollTrigger: {
                    trigger: card,
                    // start: 'top bottom',
                    toggleActions: 'play none none reverse'
                }
            })
        })
    }


    function aboutPageAnim() {
        let title = document.querySelector(".about .h2")
        let rows = title.querySelectorAll(".heading-line__cover")
        const tlService = gsap.timeline({})

        tlService.to(rows, {
            delay: .2,
            duration: 1.5,
            yPercent: -100,
            ease: "power4.out",
            stagger: 0.2,
        }).from(".about .text-bg", {
            duration: 1,
            opacity: 0,
            yPercent: 100,
        },'<').from(".service-item__desc", {
            duration: 1,
            opacity: 0,
            y: 100,
        },'<').from(".about__items", {
            duration: 1,
            opacity: 0,
            y: 100,
        },'<');

        let aboutItems = Array.from(document.querySelectorAll('.about-item'));
        aboutItems.forEach(card => {
            gsap.from(card, {
                y: 50,
                duration: .7,
                opacity: 0,
                scrollTrigger: {
                    trigger: card,
                    toggleActions: 'play none none reverse'
                }
            })
        })

        const tlSpecialization = gsap.timeline({
            scrollTrigger: {
                trigger: ".specialization",
                start: 'top bottom',
                toggleActions: 'play none none reverse'
            }
        })

        tlSpecialization.from(".specialization__title", {
            duration: .5,
            y: 50,
            opacity: 0,
        }).from(".specialization__desc", {
            duration: .3,
            y: 20,
            opacity: 0,
        }).from(".specialization__nav", {
            duration: .3,
            y: 20,
            opacity: 0,
        })
    }

    function portfolioCardsAnim() {
        let portfolioCards = Array.from(document.querySelectorAll('.portfolio-card'));
        portfolioCards.forEach(card => {
            gsap.from(card, {
                y: 50,
                opacity: 0,
                scrollTrigger: {
                    trigger: card,
                    start: 'top bottom',
                    toggleActions: 'play none none reverse'
                }
            })
        })
    }
}
